<template>
  <div class="module">
    <div class="container">
      <div class="form-box">
        <!--  -->
        <div class="register-box hidden">
          <h1>login</h1>
          <input type="text" placeholder="" v-model="tea_id" />
          <input type="password" placeholder="" v-model="tea_pwd" @keydown.13='teacherLogin' />
          <button @click.prevent="teacherLogin"></button>
        </div>
        <!--  -->
        <div class="login-box">
          <h1>login</h1>
          <input type="text" placeholder="" v-model="stu_id" />
          <input type="password" placeholder="" v-model="stu_pwd" @keydown.13='studentLogin'/>
          <button @click.prevent="studentLogin"></button>
        </div>
      </div>
      <div class="con-box left">
        <h2><span></span></h2>
        <p><span></span></p>
        <img src="./img/logo_shu.png" alt="" zoom="66%" />
        <p></p>
        <button @click="LoginS"></button>
      </div>
      <div class="con-box right">
        <h2><span></span></h2>
        <p><span></span></p>
        <img src="./img/logo_shu.png" alt="" />
        <p></p>
        <button @click="LoginT"></button>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      stu_id: "",
      stu_pwd: "",
      tea_id: "",
      tea_pwd: "",
    };
  },
  methods: {
    enterEvent(){
      console.log("OK");
    },
    //
    LoginS() {
      var login_box = document.getElementsByClassName("login-box")[0];
      var form_box = document.getElementsByClassName("form-box")[0];
      var register_box = document.getElementsByClassName("register-box")[0];
      form_box.style.transform = "translateX(0%)";
      register_box.classList.add("hidden");
      login_box.classList.remove("hidden");
    },
    LoginT() {
      var login_box = document.getElementsByClassName("login-box")[0];
      var form_box = document.getElementsByClassName("form-box")[0];
      var register_box = document.getElementsByClassName("register-box")[0];
      form_box.style.transform = "translateX(80%)";
      login_box.classList.add("hidden");
      register_box.classList.remove("hidden");
    },
    //
    async studentLogin() {
      try {
        //
        const { stu_id, stu_pwd } = this;
        stu_id &&
          stu_pwd &&
          (await this.$store.dispatch("userLogin", {
            usr: stu_id,
            pwd: stu_pwd,
            type: "s",
          }));
        let toPath = "/student";
        this.$router.push(toPath);
      } catch (error) {
        alert(error.message);
      }
    },

    async teacherLogin() {
      try {
        //
        const { tea_id, tea_pwd } = this;
        tea_id &&
          tea_pwd &&
          (await this.$store.dispatch("userLogin", {
            usr: tea_id,
            pwd: tea_pwd,
            type: "t",
          }));
        let toPath = "/teacher";
        this.$router.push(toPath);
      } catch (error) {
        alert(error.message);
      }
    },
  },
};
</script>

<style >
/*  */
* {
  margin: 0;
  padding: 0;
}
</style>

<style scoped>
/* scoped: */
.module {
  margin: 0;
  padding: 0;
  /* 100% */
  height: 100vh;
  /*  + */
  display: flex;
  justify-content: center;
  align-items: center;
  /*  */
  background: linear-gradient(200deg, #e7eff3, #e3eeff);
}
.container {
  background-color: #fff;
  width: 650px;
  height: 415px;
  border-radius: 5px;
  /*  */
  box-shadow: 5px 5px 5px rgba(0, 0, 0, 0.1);
  /*  */
  position: relative;
}
.form-box {
  /*  */
  position: absolute;
  top: -10%;
  left: 5%;
  background-color: #b7bcd8;
  width: 320px;
  height: 500px;
  border-radius: 5px;
  box-shadow: 2px 0 10px rgba(0, 0, 0, 0.1);
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 2;
  /*   */
  transition: 0.5s ease-in-out;
}
.register-box,
.login-box {
  /*   */
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 100%;
}
.hidden {
  display: none;
  transition: 0.5s;
}
h1 {
  text-align: center;
  margin-bottom: 25px;
  /*  */
  text-transform: uppercase;
  color: #fff;
  /*  */
  letter-spacing: 5px;
}
input {
  background-color: transparent;
  width: 70%;
  color: #fff;
  border: none;
  /*  */
  border-bottom: 1px solid rgba(255, 255, 255, 0.4);
  padding: 10px 0;
  text-indent: 10px;
  margin: 8px 0;
  font-size: 14px;
  letter-spacing: 2px;
}
input::placeholder {
  color: #fff;
}
input:focus {
  color: #a262ad;
  outline: none;
  border-bottom: 1px solid #a262ad80;
  transition: 0.5s;
}
input:focus::placeholder {
  opacity: 0;
}
.form-box button {
  width: 70%;
  margin-top: 35px;
  background-color: #f6f6f6;
  outline: none;
  border-radius: 8px;
  padding: 13px;
  color: #a262ad;
  letter-spacing: 2px;
  border: none;
  cursor: pointer;
}
.form-box button:hover {
  background-color: #a262ad;
  color: #f6f6f6;
  transition: background-color 0.5s ease;
}
.con-box {
  width: 50%;
  /*    */
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  /*   */
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
}
.con-box.left {
  left: -2%;
}
.con-box.right {
  right: -2%;
}
.con-box h2 {
  color: #8e9aaf;
  font-size: 25px;
  font-weight: bold;
  letter-spacing: 3px;
  text-align: center;
  margin-bottom: 4px;
}
.con-box p {
  font-size: 12px;
  letter-spacing: 2px;
  color: #8e9aaf;
  text-align: center;
}
.con-box span {
  color: #d3b7d8;
}
.con-box img {
  width: 150px;
  height: 150px;
  opacity: 0.9;
  margin: 40px 0;
}
.con-box button {
  margin-top: 3%;
  background-color: #fff;
  color: #a262ad;
  border: 1px solid #d3b7d8;
  padding: 6px 10px;
  border-radius: 5px;
  letter-spacing: 1px;
  outline: none;
  cursor: pointer;
}
.con-box button:hover {
  background-color: #d3b7d8;
  color: #fff;
}
</style>
