//API

import requests from './request';  //requestsaxios

//
export const reqUserLogin = (params) => {
  return requests({
    method: 'post',
    url: `/login`,
    data: params
  });
}

//
export const reqStuInfo = () => {
  return requests({
    method: 'post',
    url: `/student/info`,
    data: {
      type: "show"
    }
  });
}
//
export const modStuInfo = (params) => {
  return requests({
    method: 'post',
    url: `/student/info`,
    data: params,
  });
}
