//login

import {
    reqUserLogin,
} from "@/api";
import { setToken, getToken, removeToken } from "@/utils/token";
//
const state = {
    token: getToken(),
    loginType: JSON.parse(sessionStorage.getItem(`loginType`))||"",
};

//mutations:state
const mutations = {
    USERLOGIN(state, result) {
        setToken(result);
        state.token = result;
    },
    LOGINTYPE(state, result){
        sessionStorage.setItem(`loginType`, JSON.stringify(result))
        state.loginType=result;
    },
    //
    CLEAR(state) {
        //
        state.token = '';
        state.loginType = "";
        //
        sessionStorage.removeItem("loginType");
        removeToken();
    }
};

//actions:,state
const actions = {
    //
    async userLogin({ commit }, data) {
        console.log(data);
        let result = await reqUserLogin(data);
        console.log(result);
        if (result.status == 1) {
            //token
            commit("USERLOGIN", result.token);
            commit("LOGINTYPE",data.type);
            return "ok";
        } else {
            return Promise.reject(new Error(result.message));
        }
    },

      //
      async userLogout({commit}) {
        commit("CLEAR");
        return 'ok';
      },
};

//getters:
const getters = {
};

export default {
    state,
    mutations,
    actions,
    getters
};